<template>
    <div class="l-wrapper">
        <section class="l-main__auth">
            <div class="c-logo__header">
                <router-link to="/">
                    <img src="../../../img/logo.png" alt="logo" class="c-logo">
                </router-link>
            </div>
            <div class="l-card__container">
                <div class="l-card__content">
                    <div class="p-card__container">
                        <p class="p-card__title"></p>
                        <hr class="u-form__line">
                    </div>
                </div>
                <!-- end content -->
                <div class="c-form__container">
                    <form @submit.prevent="login">
                        <div class="c-form__item">
                            <label for="login-email" class="c-form-lavel"></label>
                            <!--  -->
                            <div v-if="loginErrors" class="errors">
                                <ul v-if="loginErrors.email">
                                    <li v-for="msg in loginErrors.email" :key="msg">{{ msg }}</li>
                                </ul>
                            </div>
                            <!-- end errors -->
                            <input id="login-email" type="text" class="c-input" v-model="loginFrom.email">
                        </div>
                        <!-- end c-form__item -->
                        <div class="c-form__item">
                            <label for="login-password" class="c-form-lavel"></label>
                            <!--  -->
                            <div v-if="loginErrors" class="errors">
                                <ul v-if="loginErrors.password">
                                    <li v-for="msg in loginErrors.password" :key="msg">{{ msg }}</li>
                                </ul>
                            </div>
                            <!-- end errors -->
                            <input id="login-password" type="password" class="c-input" v-model="loginFrom.password">
                        </div>
                        <!-- end c-form__item -->
                        <div class="c-form__action">

                            <div class="c-form__action__item">
                                <button type="submit" class="c-btn c-btn__login"></button>
                            </div>
                        </div>
                            <div class="c-btn-reminder">
                                <router-link to="/reminder"></router-link>
                            </div>
                    </form>
                    <hr class="u-form__line">
                    <div class="c-form__action c-form__action--easy">
                        <div class="u-social__item">
                            <button class="c-btn c-btn__easy" @click="gestUser"></button>
                        </div>
                        <div class="u-social__item">
                            <button class="c-btn c-btn__twitter" @click="twitterlogin">Twitter</button>
                        </div>
                    </div>
                </div>
                <!-- c-form__container -->
            </div>
            <!-- l-card__container -->
        </section>
        <!-- l-main__auth -->
    </div>
    <!-- l-wrapper__login -->
</template>
<script>
import { mapState, mapGetters } from 'vuex';
export default {
  data (){
    return {
        loginFrom: 
          {
            email:'',
            password:''
          }
    }
  },
  computed: {
    // ...mapState
    ...mapState({
      apiStatus: state => state.auth.apiStatus,
      loginErrors: state => state.auth.loginErrorMessages
    })
  },
  methods: {
    // authlogin
    async login(){
      await this.$store.dispatch('auth/login', this.loginFrom);
      if(this.apiStatus){
        // apiStatusture
        this.$router.push('/tasklist');
      }
    },
    async gestUser(){
      await this.$store.dispatch('auth/gestUser', {
        email: 'test01@mail.com',
        password: 'password'
      });
      
      if(this.apiStatus){
        // apiStatusture
        this.$router.push('/tasklist');
      }
    },
    clearError(){
      this.$store.commit('auth/setLoginErrorMessages', null);
    },
    async twitterlogin(){
      await axios.get('/api/twitter')
      .then(response => {
        const URL = response.data.redirect_url
        window.location = URL
      }).
      catch(error => error.response || error);
    }
  },
    created(){
    // created
    this.clearError();
  }
}
</script>
