<template>
  <div>
    <Header/>
    <div class="l-wrapper l-wrapper__profile">
      <transition name="flash">
      <Message v-show="success"/>
      </transition>
      <div class="l-main__auth l-main__auth--profile">
        <div class="l-card__container">
          <div class="p-card__container">
            <p class="p-card__title"></p>
          </div>
          <hr class="u-form__line">
          <div class="c-form__container--profile">
            <div class="c-form__item">
              <label for="" class="c-form-lavel c-form-lavel__profile"></label>
              <!--  --->
              <div v-if="profileUploadErrors" class="errors errors--profile">
                <ul v-if="profileUploadErrors.profilePhoto">
                  <li v-for="msg in profileUploadErrors.profilePhoto" :key="msg">{{ msg }}</li>
                </ul>
              </div>
              <!--- end errors -->
              <label class="c-input--profile" >
                <input type="file" class="c-input--profile__drop" @change="fileSelected" @focus="imagefocus">
                <output v-if="preview">
                  <img v-bind:src="preview" alt="" class="c-form__outputImg">
                  <p v-show="PreviewProfileImage">
                    <img v-bind:src="profileData.pic" alt="" class="c-form__output">
                  </p>
                </output>
                <output v-else>
                  <img src="../../img/no_img/no_img.png" alt="" class="c-form__outputImg">
                </output>
              </label>
              <div class="l-flex l-flex--center u-btn--wrapp" v-show="showProfileImage">
                <div class="u-btn__profile--margin">
                  <button class="c-btn c-btn--profile c-btn--profile__cancel" @click="reset"></button>
                </div>
                <div class="u-btn__profile--margin">
                  <button class="c-btn c-btn--profile" @click="ProfileImageEdit"></button>
                </div>
              </div>
              <!-- l-flex -->
            </div>
            <!-- end c-form__item -->
            <div>
              <div class="c-form__item">
                <label for="" class="c-form-lavel"></label>
                <!--  --->
                <div v-if="profileUploadErrors" class="errors">
                  <ul v-if="profileUploadErrors.name">
                    <li v-for="msg in profileUploadErrors.name" :key="msg">{{ msg }}</li>
                  </ul>
                </div>
                <!--- end errors -->
                <input type="text" class="c-input" v-model="profileData.name" @focus="namefocus">
                <!--  -->
                <div class="l-flex u-btn--wrapp" v-show="showName">
                  <div class="u-btn__profile--margin">
                    <button class="c-btn c-btn--profile c-btn--profile__cancel" @click="cancelName"></button>
                  </div>
                  <div class="u-btn__profile--margin">
                    <button class="c-btn c-btn--profile" @click="ProfileNameEdit"></button>
                  </div>
                </div>
                <!-- l-flex -->
              </div>
              <div class="c-form__item">
                <label for="" class="c-form-lavel"></label>
                <!--  --->
                <div v-if="profileUploadErrors" class="errors">
                  <ul v-if="profileUploadErrors.email">
                    <li v-for="msg in profileUploadErrors.email" :key="msg">{{ msg }}</li>
                  </ul>
                </div>
                <!--- end errors -->
                <input type="text" class="c-input" v-model="profileData.email" @focus="emailfocus">
              </div>
              <!--  -->
              <div class="l-flex u-btn--wrapp" v-show="showEmail">
                <div class="u-btn__profile--margin">
                  <button class="c-btn c-btn--profile c-btn--profile__cancel" @click="canselEmail"></button>
                </div>
                <div class="u-btn__profile--margin">
                  <button class="c-btn c-btn--profile" @click="ProfileEmailEdit"></button>
                </div>
              </div>
              <!-- l-flex -->
              <div class="u-btn--wrapp u-btn--password">
                <button class="c-btn c-btn--profile" @click="showPassword = !showPassword"></button>
              </div>
            </div>
          </div>
          <!-- c-form__container--profile -->
          <transition name="modal">
            <div key="modal" class="c-modal" v-show="showPassword">
              <div class="c-modal--body">
                <div class="p-nav--trigger" @click="ShowPasswordTrigger">
                  <i class="fas fa-times p-nav--close"></i>
                </div>
                <div class="c-form__item c-modal--inner">
                  <label for="" class="c-form-lavel"></label>
                  <!--  --->
                  <div v-if="profileUploadErrors" class="errors">
                    <ul v-if="profileUploadErrors.password">
                      <li v-for="msg in profileUploadErrors.password" :key="msg">{{ msg }}</li>
                    </ul>
                  </div>
                  <!--- end errors -->
                  <input type="password" class="c-input" v-model="profileData.password">
                </div>
                <!-- c-form__item -->
                <div class="c-form__item">
                  <label for="" class="c-form-lavel"></label>
                  <!--  --->
                  <div v-if="profileUploadErrors" class="errors">
                    <ul v-if="profileUploadErrors.password_confirmation">
                      <li v-for="msg in profileUploadErrors.password_confirmation" :key="msg">{{ msg }}</li>
                    </ul>
                  </div>
                  <!--- end errors -->
                  <input type="password" class="c-input" v-model="profileData.password_confirmation">
                </div>
                <div class="u-btn--wrapp u-btn--password c-modal--btn__passwordEdit">
                  <button class="c-btn c-btn--profile" @click="ProfilPasswordeEdit"></button>
                </div>
              </div>
            </div>
            <!-- end c-modal -->
          </transition>
        </div>
        <!-- l-card__container -->
        <div class="l-card__container">
          <div class="p-card__container">
            <p class="p-card__title"></p>
          </div>
          <hr class="u-form__line">
          <div class="c-form__container">
            <p></p>
          </div>
          <div class="c-form__action c-form__action__item">
            <button type="submit" class="c-btn c-btn__danger" @click="userSoftDelete"></button>
          </div>
        </div>
        <!-- l-card__container -->
      </div>
      <!-- l-main__auth -->
    </div>
    <!-- l-wrapper__profile -->
  </div>
</template>
<script>
import { mapState, mapGetters } from 'vuex';
import Header from './Header';
import Message from './Message';
export default {
  data(){
    return {
      // 
      showProfileImage: false,
      showName: false,
      showEmail: false,
      showPassword: false,
      
      // 
      preview: null,
      PreviewProfileImage: null,

      // 
      success: false,

      // 
      profileData: 
          { 
            name:'',
            email:'',
            password:'',
            password_confirmation: '',
            profileImage: null,
          }
    }
  },
    components: {
    Header,
    Message
  },
  computed: {
    ...mapState({
      profileUploadErrors: state => state.auth.profileErrorMessages,
    }),
    ...mapGetters({
      getCode: 'error/getCode'
    })
  },
  methods: {
      // 
      fileSelected(event){
        // 
        if(!event.target.files[0].type.match('image.*')){
          this.$store.commit('auth/setProfileErrorMessages', { profilePhoto: [''] });
          this.$el.querySelector('input[type="file"]').value = null;
          return false;
        }
        // data
        // Eventtargetfiles
        this.profileImage = event.target.files[0];
        // FileReader
        const reader = new FileReader();
        // 
        reader.onload = event => {
          this.preview = event.target.result;
        }
        // 
        // URL
        reader.readAsDataURL(event.target.files[0]);
        // 
        this.profileData.profileImage = event.target.files[0];
      },
      reset(){
        // 
        this.clearError();
        // this.$el.querySelectorinputDOM
        this.$el.querySelector('input[type="file"]').value = null;
        this.showProfileImage = !this.showProfileImage;
      },
      // 
      async ProfileImageEdit(){
        this.clearError();
        // 
        // FormData
        const formData = new FormData();
        // append12value
        // 
        // 
        // formdata https://qiita.com/_Keitaro_/items/6a3342735d3429175300
        formData.append('profilePhoto', this.profileData.profileImage);
        // 
        await this.$store.dispatch('auth/ProfileImageEdit', formData);
        if(this.getCode === 200){
          this.showSuccess();
          setTimeout(this.showSuccess, 3000);
        }
        this.showProfileImage = !this.showProfileImage
      },
      async userSoftDelete(){
        if(this.$store.state.auth.user_id === 1){
          alert('');
          return false;
        }

        if(window.confirm('\n')){
          // 
          await this.$store.dispatch('auth/userSoftDelete');
          this.$router.push('/register');
        }
      },
      async ProfileNameEdit(){
        this.clearError();
        // 
        await this.$store.dispatch('auth/ProfileNameEdit', { name: this.profileData.name } );
        if(this.getCode === 200){
          this.showName = !this.showName;
          this.showSuccess();
          setTimeout(this.showSuccess, 3000);
        }
      },
      async ProfileEmailEdit(){
        this.clearError();
        // 
        await this.$store.dispatch('auth/ProfileEmailEdit', { email: this.profileData.email } );
        if(this.getCode === 200){
          this.showEmail = !this.showEmail;
          this.showSuccess();
          setTimeout(this.showSuccess, 3000);
        }
      },
      async ProfilPasswordeEdit(){
        this.clearError();
        // 
        await this.$store.dispatch('auth/ProfilPasswordeEdit', 
        { 
          // key:value
          password: this.profileData.password,
          password_confirmation: this.profileData.password_confirmation
        })
  
        if(this.getCode === 200){
          // 
          this.profileData.password = '';
          this.profileData.password_confirmation = '';
          this.showPassword = !this.showPassword;
          this.clearError();
          this.showSuccess();
          setTimeout(this.showSuccess, 3000);
        }
      },
      cancelName(){
        this.clearError();
        this.showName = !this.showName;
      },
      canselEmail(){
        this.clearError();
        this.showEmail = !this.showEmail;
      },
      cancelPassword(){
        this.profileData.password = '';
        this.profileData.password_confirmation = "";
        this.clearError();
        this.showPassword = !this.showPassword;
      },
      ShowPasswordTrigger(){
        this.profileData.password = '';
        this.profileData.password_confirmation = "";
        this.clearError();
        this.showPassword = !this.showPassword;
      },
      /****************************************
       * focus
      *****************************************/
      namefocus(){
           if(this.showName === true){
          return;
        }
        this.showName = !this.showName;
      },
      emailfocus(){
        if(this.showEmail === true){
          return;
        }
        this.showEmail = !this.showEmail;
      },
      imagefocus(){
        if(this.showProfileImage === true){
          return;
        }
        this.showProfileImage = !this.showProfileImage;
      },
      /*************************************************
       * 
      **************************************************/
      clearError(){
        this.$store.commit('auth/setProfileErrorMessages', null);
      },
      /*************************************************
       * 
      **************************************************/
      showSuccess(){
        this.success = !this.success;
      },
      /*************************************************
       * 
      **************************************************/
      getProfile(){
        axios.get('/api/user').then(response => {
          this.profileData.name = response.data.name;
          this.profileData.email = response.data.email;
          this.preview = response.data.pic;
        }).catch( error => {
          console.log(error);
        })
      }
  },
  created(){
    // created
    this.clearError();
    this.getProfile();
  }
}
</script>
<style>
.modal-enter-active, .modal-leave-active {
  transition: all .4s ease;
}
.modal-enter, .modal-leave-to {
  opacity: 0;
}

.flash-enter-active, .flash-leave-active {
  transition: all .6s ease;
}
.flash-enter, .flash-leave-to {
  opacity: 0;
  transform: translateX(50px)
}
</style>