require('./bootstrap');

import Vue from 'vue'
import App from './App.vue'
import store from './store/store'
import router from './router'

// Vue.config.devtools = false

// Vue
// awaitasync
// createApp()Vue
const createApp = async function(){
  await store.dispatch('auth/currentUser')

    new Vue({
      router,
      store,
      
      // h(App)App.vue
      // render 
      render: h => h(App)
    }).$mount('#app')

    // new Vue({
    //   el: '#app',
    //   router, // 
    //   store,
    //   components: { App }, // 
    //   template: '<App />' // 
    // })

}

createApp()

