<template>
    <div class="v-div">
        <div v-show="!CardEdit_flg" class="c-task--todo--list c-task--todo--push" @click="CardEdit_flg = !CardEdit_flg">  </div>
        <div v-show="CardEdit_flg" class="c-task--todo--inputAreaWrapp cardAdd">
            <form @submit.prevent>
                <input type="text" class="c-task--todo--inputArea"
                :class="{'errors--bg': cardRequestErrorMessages}"
                v-model="CradCreateForm"
                >
                <!--  --->
                    <ul v-if="cardRequestErrorMessages" class="errors errors--tasks">
                        <p v-for="(msg, index) in cardRequestErrorMessages.title" :key="index">{{ msg }}</p>
                    </ul>
                <!--- end errors -->
                <div class="l-flex u-btn--wrapp">
                    <div class="u-btn__profile--margin">
                        <button class="c-btn c-btn--profile c-btn--profile__cancel" @click="clearCradCreateForm"></button>
                    </div>
                    <div class="u-btn__profile--margin">
                        <button type="submit" class="c-btn c-btn--profile" @click="createCard"></button>
                    </div>
                </div>
                <!-- l-flex -->
            </form>
        </div>
        <!-- c-task--todo--inputAreaWrapp -->
    </div>
</template>
<script>
import { OK, UNPROCESSABLE_ENTITY, INTERNAL_SERVER_ERROR, CREATED } from '../statusCode';

export default {
  data(){
    return{
      CardEdit_flg: null,
      CradCreateForm: '',
      folder_id: ''
    }
  },
  computed: {
    cardRequestErrorMessages(){
    // 
    return this.$store.state.taskStore.cardRequestErrorMessages
    },
    getErrorCode(){
      return this.$store.state.error.code
    }
  },
  methods: {
    // 
    async createCard(){
      this.$store.commit('taskStore/setCardRequestErrorMessages', null);
      // ID
      this.folder_id = this.$store.state.taskStore.folder_id;
      if(!this.folder_id){
        const data = {
            title: [
              ''
            ]
        }
        this.$store.commit('taskStore/setCardRequestErrorMessages', data);
        return false;
      }
      
      // ID
      await this.$store.dispatch('taskStore/createCard',
      { 
        // 2
        // 
        title: this.CradCreateForm,
        folder_id: this.folder_id
      })

        if(this.getErrorCode === OK){
          await this.$store.dispatch('taskStore/setCardListsAction', this.folder_id )
          this.clearCradCreateForm();
        }
      // 
      await this.$store.dispatch('taskStore/setCardListsAction', this.folder_id );

    },
    // 
    clearCradCreateForm(){
    this.CradCreateForm = '';
    this.CardEdit_flg = !this.CardEdit_flg;
    this.clearError();
    },
    /*************************************************
     * 
    **************************************************/
    clearError(){
      this.$store.commit('taskStore/setCardRequestErrorMessages', null);
    },
  }
}
</script>
<style>
.v-div{
  padding: 0 20px;
  max-width: 220px;
}
.cardAdd{
  padding: 0 20px 0 0;
}
</style>