import { OK, UNPROCESSABLE_ENTITY, INTERNAL_SERVER_ERROR ,CREATED } from '../statusCode'

const state = {
  username: null,
  user_id: null,
  email: null,
  password: null,
  profileImage: null,
  // 200 403 500
  apiStatus: null,

  /****************************************
  
  *****************************************/
  sendEmailMessages: null,

  /****************************************
  
  *****************************************/
  loginErrorMessages: null,
  registerErrorMessages: null,
  profileErrorMessages: null,
  reminderErrorMessages: null,
  resetPasswordErrorMessages: null,
  contactMailErrorMessages: null
}

/***********************************

************************************/
const getters = {
  // 
  check: state => !! state.username,
  // 
  apiStatus: state => state.apiStatus ? state.apiStatus : '',
  // usernameusernull
  getUserName: state => state.username ? state.username : '',
  // email
  getEmail: state => state.email ? state.email : '',
  // 
  getProfileImage: state => state.profileImage ? state.profileImage : '',
}

/*******************************

********************************/
const mutations = {
  // 
  setUser(state, username){
    state.username = username
  },
  // ID
  setId(state, user_id){
    state.user_id = user_id
  },
  // email
  setEmail(state, email){
    state.email = email
  },
  // 
  setPic(state, profileImage){
    state.profileImage = profileImage
  },
  // 
  setApiStatus(state, status){
    state.apiStatus = status
  },
  // 
  setLoginErrorMessages(state, messages) {
    state.loginErrorMessages = messages
  },
  // 
  setRegisterErrorMessages(state, messages) {
    state.registerErrorMessages = messages
  },
  // 
  setProfileErrorMessages(state, messages){
    state.profileErrorMessages = messages
  },
  // 
  setReminderErrorMessages(state, message){
  state.reminderErrorMessages = message
  },
  // 
  setResetPasswordErrorMessages(state, message){
  state.resetPasswordErrorMessages = message
  },
  setContactMailErrorMessages(state, message){
    state.contactMailErrorMessages = message
  },


  /****************************************
  
  *****************************************/
  sendEmailMessages(state, message){
    state.sendEmailMessages = message
  },
}

// 
const actions = {
  /****************************************
  
  *****************************************/
  // 1commit
  // 2
  async register( {commit} , data){
    // commitsetApiStatusnull
    commit('setApiStatus', null)
    const response = await axios.post('/api/register', data).catch(error => error.response || error)
      // 200
      if(response.status === CREATED){
        const username = response.data.name;
        const id = response.data.id;
        // 
        commit('setApiStatus', true)
        commit('setUser', username) 
        // ID
        commit('setId', id )
        return false
      }
    commit('setApiStatus', false)
    // 422
    if(response.status === UNPROCESSABLE_ENTITY ){
      commit('setRegisterErrorMessages', response.data.errors)
    } else {
      commit('error/setCode', response.status, { root: true })
    }
    commit('error/setCode', response.status, { root: true }) //{ root: ture }
  },
  /****************************************
  
  *****************************************/
  async login( {commit} , data){
    // commitsetApiStatusnull
    commit('setApiStatus', null)
      // axiosLaravelAPIJSON
      const response = await axios.post('/api/login', data).catch(error => error.response || error)
        // 200
        if(response.status === OK){

            if(response.data.errors){
              console.log(response.data.errors)
              commit('setLoginErrorMessages', response.data.errors)
              return false;
            }
          const username = response.data.name
          const email = response.data.email
          const profileImage = response.data.pic
          const id = response.data.id
          // 
          commit('setApiStatus', true)
          commit('setUser', username) 
          commit('setEmail', email) 
          commit('setPic', profileImage) 
          // ID
          commit('setId', id )
          return false
        }
      commit('setApiStatus', false)
      // 422
      if(response.status === UNPROCESSABLE_ENTITY ){
        commit('setLoginErrorMessages', response.data.errors)
      } else {
        commit('error/setCode', response.status, { root: true })
      }
      commit('error/setCode', response.status, { root: true }) //{ root: ture }
  },
  /****************************************
  
  *****************************************/
  async gestUser({commit} , data){
    commit('setApiStatus', null)
    // axiosLaravelAPIJSON
    const response = await axios.post('/api/login', data).catch(error => error.response || error)
        // 200
        if(response.status === OK){

            if(response.data.errors){
              console.log(response.data.errors)
              commit('setLoginErrorMessages', response.data.errors)
              return false;
            }
          const username = response.data.name
          const email = response.data.email
          const profileImage = response.data.pic
          const id = response.data.id
          // 
          commit('setApiStatus', true)
          commit('setUser', username) 
          commit('setEmail', email) 
          commit('setPic', profileImage) 
          // ID
          commit('setId', id )
          return false
        }
      commit('setApiStatus', false)
      // 422
      if(response.status === UNPROCESSABLE_ENTITY ){
        commit('setLoginErrorMessages', response.data.errors)
      } else {
        commit('error/setCode', response.status, { root: true })
      }
      commit('error/setCode', response.status, { root: true }) //{ root: ture }
  },
  /****************************************
  
  *****************************************/
  async logout ({commit}) {
    commit('setApiStatus', null)
    const response = await axios.post('/api/logout').catch(error => error.response || error)
    if(response.status === INTERNAL_SERVER_ERROR ){
      commit('error/setCode', response.status, { root: true })
    }else if(response.status === OK){
      commit('setApiStatus', null)
      commit('setUser', null)
      commit('setEmail', null) 
      commit('setPic', null) 
      commit('setId', null)
      return false
    }

    commit('setApiStatus', false)
    commit('error/setCode', response.status, { root: true })
  },
  /****************************************
  
  *****************************************/
  // 
  async ProfileImageEdit( {commit}, data){
    const id = state.user_id
    const response = await axios.post('/api/profile/image/' + id , data).catch(error => error.response || error)
    // 422
    if(response.status === UNPROCESSABLE_ENTITY ){
      commit('setProfileErrorMessages', response.data.errors)
    } else {
      commit('error/setCode', response.status, { root: true })
    }
    commit('error/setCode', response.status, { root: true }) //{ root: ture }
  },
  // 
  async ProfileNameEdit( {commit}, data){
    const id = state.user_id
    const response = await axios.post('/api/profile/name/' + id , data).catch(error => error.response || error)
    // 422
    if(response.status === UNPROCESSABLE_ENTITY ){
      commit('setProfileErrorMessages', response.data.errors)
    } else {
      commit('error/setCode', response.status, { root: true })
    }

      if(response.data.name){
        commit('setUser', response.data.name) 
      }
    commit('error/setCode', response.status, { root: true })

  },
  // email
  async ProfileEmailEdit( {commit}, data){
    const id = state.user_id
    const response = await axios.post('/api/profile/email/' + id , data).catch(error => error.response || error)
    // 422
    if(response.status === UNPROCESSABLE_ENTITY ){
      commit('setProfileErrorMessages', response.data.errors)
    } else {
      commit('error/setCode', response.status, { root: true })
    }
    commit('error/setCode', response.status, { root: true })
  },
  // 
  async ProfilPasswordeEdit( {commit}, data){
    const id = state.user_id
    const response = await axios.post('/api/profile/password/' + id , data).catch(error => error.response || error)
    // 422
    if(response.status === UNPROCESSABLE_ENTITY ){
      commit('setProfileErrorMessages', response.data.errors)
    } else {
      commit('error/setCode', response.status, { root: true })
    }
    commit('error/setCode', response.status, { root: true })
  },
  // 
  async userSoftDelete({ commit }){
    commit('setApiStatus', null)
    const id = state.user_id
    const response = await axios.delete('/api/profile/delete/' + id).catch(error => error.response || error)
    if(response.status === INTERNAL_SERVER_ERROR ){
      commit('error/setCode', response.status, { root: true })
    }else if(response.status === OK){
      console.log('')
      commit('setApiStatus', null)
      commit('setUser', null)
      commit('setEmail', null) 
      commit('setPic', null) 
      commit('setId', null)
      return false
    }
    commit('setApiStatus', false)
    commit('error/setCode', response.status, { root: true })
  },
  /****************************************
  
  *****************************************/
  async currentUser ({commit}) {
    const response = await axios.get('/api/user')    
    const loginUser = response.data || null
    if(loginUser){
      commit('setApiStatus', true)
      commit('setUser', loginUser.name) 
      commit('setEmail', loginUser.email) 
      commit('setPic', loginUser.pic) 
      commit('setId', loginUser.id )
    }
  },
  /****************************************
  
  *****************************************/
  async sendResetLinkEmail({commit}, data){
    const response = await axios.post('/api/password/reminder', {email: data}).catch(error => error.response || error)
    console.log(response.data.errors)
      // 422
      if(response.status === UNPROCESSABLE_ENTITY ){
        commit('setReminderErrorMessages', response.data.errors)
      } else {
        commit('error/setCode', response.status, { root: true })
        commit('sendEmailMessages', response.data.success)
      }
      commit('error/setCode', response.status, { root: true }) //{ root: ture }
  },
  /****************************************
  
  *****************************************/
  async resetPassword({commit}, data){
    const response = await axios.post('/api/password/reset', data ).catch(error => error.response || error)
    if(response.status === UNPROCESSABLE_ENTITY){
      commit('setResetPasswordErrorMessages', response.data.errors)
    } else {
      commit('error/setCode', response.status, { root:true })
    }
    commit('error/setCode', response.status, { root: true })
  },
  /****************************************
  
  *****************************************/
  async contactMessage({commit}, data){
    const response = await axios.post('/api/contact', data).catch(error => error.response || error)
      if(response.status === UNPROCESSABLE_ENTITY){
        commit('setContactMailErrorMessages', response.data.errors)
      } else {
        commit('error/setCode', response.status, { root:true })
      }
      commit('error/setCode', response.status, { root:true })
    }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}