import Vue from 'vue'
import VueRouter from 'vue-router'


// 
import Index from './components/index'
import TaskApp from './components/TaskApp'
import Profile from './components/Profile'
import Policy from './components/Policy'
import Rule from './components/Rule'
import Callback from './components/Callback'
import Login from './components/page/Login'
import Register from './components/page/Register'
import Contact from './components/page/Contact'
import PasswordReminder from './components/page/PasswordReminder'
import PasswordReset from './components/page/PasswordReset'
import SystemError from './components/page/error/SystemError.vue'

import store from './store/store'

// vuex


// vue-router
Vue.use(VueRouter);

//vue-router
export default new VueRouter({

  mode: 'history', // history
  routes: [
    {
      // 
      path: '*',
      redirect: '/'
    },
    {
      // Top
      path: '/',
      component: Index
    },
    {
      // 
      path: '/login',
          component: Login,
          // 
          beforeEnter(to, from, next){
            // 
            if(store.getters['auth/check']){
              next('/tasklist')
            }else{
              next()
            }
          }
    },
    {
      // 
      path: '/register',
      component: Register,
          // 
          beforeEnter(to, from, next){
            // 
            if(store.getters['auth/check']){
              next('/tasklist')
            }else{
              next()
            }
          }
    },
    {
      // 
      path: '/reminder',
      component: PasswordReminder
    },
    {
      // 
      path: '/reset',
      component: PasswordReset
    },
    {
      // 
      path: '/tasklist',
      component: TaskApp,
        // 
        beforeEnter(to, from, next){
        // 
        if(store.getters['auth/check']){
          next()
        }else{
          // 
          next('/login')
        }
      }
    },
    {
      // 
      path: '/profile',
      component: Profile,
        // 
        beforeEnter(to, from, next){
        // 
        if(store.getters['auth/check']){
          // next('/profile')
          next()
        }else{
          // 
          next('/login')
        }
      }
    },
    {
      // 
      path: '/contact',
      component: Contact
    },
    {
      // 
      path: '/policy',
      component: Policy
    },
    {
      // 
      path: '/rule',
      component: Rule
    },
    {
      // 
      path: '/500',
      component: SystemError
    },
    {
      // 
      path: '/callback',
      component: Callback
    }
  ],
  // 
  scrollBehavior(){
    return new Promise(resolve => {
      setTimeout( () => {
        resolve({ x:0, y:0 })
      }, 500)
    })
  }
})