<template>
    <div>
        <!-- TaskList -->
        <div class="c-task--card__inner">
            <div class="c-task--todo">
                <div class="c-task--todo__header">
                    <span v-if="!editFlag" @dblclick="editCard" @touchstart="editCard">{{ cardTitle }} </span>
                    <form class="c-updateFrom" @submit.prevent v-else>
                        <!--  --->
                        <ul v-if="cardRequestErrorMessages" class="errors errors--tasks">
                            <li v-for="(msg, index) in cardRequestErrorMessages.title" :key="index">{{ msg }} </li>
                        </ul>
                        <!--- end errors -->
                        <input type="text" class="c-input c-input--tasks"
                        v-model="cardTitle"
                        @keypress.enter="updateCardTitle"
                        @keyup.esc="cancelEdit"
                        @blur="cancelEdit"
                        :class="{'errors--bg': cardRequestErrorMessages}"
                        >
                    </form>
                    <label for="" class="c-task--todo--counter">{{ listCounter }} </label>
                    <div class="c-task--todo--card--del">
                        <i class="fas fa-times" @click="deleteCard"></i>
                    </div>
                </div>
                <TaskListAdd :cards="cards"/>
                <!-- c-task--todo--push -->
                <draggable
                :list="cards.tasks"
                v-bind="{group :'cards.tasks', animation: 300}"
                @add="onAdd"
                @change="onChange"
                handle='.hand-icon'
                :data-card-id="cards.id">
                    <TaskList v-for="(task, index) in cards.tasks"
                    :key="task.id"
                    :id="task.id"
                    :title="task.title"
                    :listIndex="index"
                    :created_at="task.created_at"
                    :cards="cards"/>
                </draggable>
                <!-- <TaskList :cards="cards" /> -->
            </div>
            <!-- end c-task--todo -->
        </div>
        <!-- end c-task--card__inner -->
        <!-- TaskCard -->
    </div>
</template>
<script>
import { OK } from '../statusCode';
import { mapState, mapGetters } from 'vuex';
import draggable from 'vuedraggable';
import TaskList from './TaskList';
import TaskListAdd from './TaskListAdd';
import TaskCardAdd from './TaskCardAdd';
export default {
  data() {
      return {
        editFlag: false,
        cardTitle: this.cards.title
    }
  },
  props: {
    cards: {
      type: Object,
      required: true
    },
    id: {
      type: Number,
      required: true
    },
    listIndex: {
      type: Number,
      required: true
    }
  },
  components: {
    TaskList,
    TaskListAdd,
    TaskCardAdd,
    draggable
  },
  computed:{
    listCounter(){
      return this.cards.tasks.length;
    },
    ...mapState({
      cardRequestErrorMessages: state => state.taskStore.cardRequestErrorMessages
    }),
    ...mapGetters({
      getCode: 'error/getCode'
    })
  },
  methods: {
    // 
    async deleteCard(){
      const folder_id = this.cards.folder_id;
      const card_id = this.cards.id;
      // console.log( 'id' + folder_id + '  ' + 'id' + card_id)
      if(window.confirm('\n')){
        await this.$store.dispatch('taskStore/deleteCard', 
        {
          folder_id: folder_id,
          card_id: card_id
        })

        if(this.getCode === OK){
          // 
          await this.$store.dispatch('taskStore/setCardListsAction', folder_id );
        }
      }
    },
    // 
    async updateCardTitle(){
      const folder_id = this.cards.folder_id
      const card_id = this.cards.id
      await this.$store.dispatch('taskStore/updateCardTitle',
      {
        title: this.cardTitle,
        folder_id: folder_id,
        card_id: card_id
      })

      if(this.getCode === OK){
        await this.$store.dispatch('taskStore/setCardListsAction', folder_id );
        this.editCard();
      // 
      }
    },
    // 
    async updateTaskDraggable(cardId, taskId){
      await this.$store.dispatch('taskStore/updateTaskDraggable',
      {
        cardId: cardId,
        task_id: taskId
      })
    },
    // 
    async updateTaskSort(newTasks){
      await this.$store.dispatch('taskStore/updateTaskSort', newTasks);
      const folder_id = this.cards.folder_id
      if(this.getCode === OK){
        await this.$store.dispatch('taskStore/setCardListsAction', folder_id );
      // 
      }
    },
    // 
    editCard(){
      this.editFlag = !this.editFlag;
      this.clearError();
    },
    // 
    cancelEdit(){
      this.editFlag = false;
      // propsdata
      this.cardTitle = this.cards.title;
      this.clearError();
    },
    /*************************************************
    * 
    **************************************************/
    clearError(){
      this.$store.commit('taskStore/setCardRequestErrorMessages', null);
    },
    // draggable
    onAdd(event){
      let fromCradId = event.from.getAttribute("data-card-id");
      let taskId = event.item.getAttribute("data-task-id");
      let toCardId = event.to.getAttribute("data-card-id");
      this.updateTaskDraggable(toCardId, taskId)
    },
    // draggable
    // https://www.ritolab.com/entry/173
    onChange(){
      let newTasks = this.cards.tasks.map((task, index) =>{
        task.priority = index +1;
        return task;
      })
      this.updateTaskSort(newTasks);
    }
  },
}
</script>
